<template>
  <div class="properties-list">
    <section class="properties-header">
      <div class="container">
        <ul>
          <li v-if="stateSelected.attributes">{{ this.stateSelected.attributes.name }}</li>
          <li v-if="citySelected.attributes"><span>â€¢</span>{{ this.citySelected.attributes.name }}</li>
        </ul>
      </div>
    </section>
    <section class="properties-filter">
      <div class="container">
        <div class="search">
          <img src="~@/assets/icons/icon-search.svg" alt="">
          <input type="text" placeholder="Buscar" v-model="searchText" @change="searchChange()" />
          <a @click="searchChange()" class="button-primary"><img src="~@/assets/icons/icon-search-white.svg" alt=""></a>
        </div>
        <select class="select" v-model="stateSelected" v-on:change="stateChange">
          <option value="">Local</option>
          <option v-for="item in this.states" :value="item" :key="item.id">{{ (item.attributes.name == "Todos") ? "Todos locais" : item.attributes.name }}</option>
        </select>
        <select class="select" v-model="citySelected" v-on:change="cityChange">
          <option value="">Cidade</option>
          <option v-for="item in this.cities" :value="item" :key="item.id" >{{ (item.attributes.name == "Todos") ? "Todas cidades" : item.attributes.name  }}</option>
        </select>
        <select class="select" v-model="benefitsSelected" v-on:change="benefitsChange">
          <option value="">Vantagens</option>
          <option v-for="item in this.benefits" :value="item" :key="item.id" >{{ (item.attributes.name == "Todos") ? "Todas vantagens" : item.attributes.name  }}</option>
        </select>
      </div>
    </section>
    <div v-if="loader" class="loader">
      <img src="~@/assets/loader.gif" alt="">
      <p>Carregando imÃ³veis...</p>
    </div>
    <div v-if="!loader && empty" class="empty">
      <img src="~@/assets/image-empty.svg" alt="">
      <h3>Oops!</h3>
      <p>NÃ£o foi possivel encontrar o que estÃ¡ procurando, filtre novamente e encontre o imÃ³vel ideal.</p>
    </div>
    <section v-if="!loader" class="properties-section">
      <div class="container container-properties">
        <div
          class="box-properties"
          v-for="item in this.properties"
          :value="item.id"
          :key="item.id"
        >
          <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
            <img v-if="item.attributes.building.banner_url" class="properties-image" alt="" :src="item.attributes.building.banner_url">
            <img v-if="!item.attributes.building.banner_url" class="properties-image" src="~@/assets/placeholder-properties.png" alt="">

            <div class="properties-content">
              <p class="tag">{{ getStatus(item.attributes.status) }}</p>
              <div>
                <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
                  <img class="logo" alt="" :src="item.attributes.building.logo_url">
                </router-link>

                <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
                  <h3>{{ item.attributes.name }}</h3>
                </router-link>

                <router-link :to="{ name: 'Properties', params: { slug: item.attributes.slug, id: item.id } }" exact>
                  <p class="locale">{{ item.attributes.address }}</p>
                </router-link>

                <p
                  class="infos"
                  v-for="features in item.attributes.features.slice(0,2)"
                  :value="features.id"
                  :key="features.id"
                >
                  {{ features.name }}
                </p>
              </div>
            </div>
          </router-link>
        </div>
      </div>
      <div class="view-more" v-if="toggleButtonMore">
        <a @click="viewMore()">Carregar mais</a>
      </div>
    </section>
  </div>
</template>

<script>
import { propertiesService } from '@/services/properties.service'
import { regionsService } from '@/services/regions.service'

export default {
  name: 'PropertiesList',
  data () {
    return {
      loader: false,
      empty: false,
      firstValue: { id: '', attributes: { name: 'Todos' } },
      properties: [],
      states: [],
      cities: [],
      benefits: [],
      prices: [],
      stateSelected: {},
      citySelected: {},
      priceSelected: '',
      sortType: '',
      benefitsSelected: '',
      searchText: undefined,
      currentPage: 1,
      perPage: 12,
      totalItems: 0,
      totalPages: 0,
      toggleButtonMore: true
    }
  },

  mounted () {
    const state = this.$route.params.state
    const city = this.$route.params.city

    if (state != null && city != null) {
      this.retrieveValuesFromHome(state, city)
    } else {
      this.initialize()
      this.getStates()
    }

    this.getProperties()
    this.getBenefits()
  },

  methods: {
    initialize () {
      this.states = [this.firstValue]
      this.cities = [this.firstValue]
      this.benefits = [this.firstValue]
      this.stateSelected = this.firstValue
      this.citySelected = this.firstValue
      this.benefitsSelected = this.firstValue
    },

    viewMore () {
      if (this.currentPage < this.totalPages) {
        this.perPage = this.perPage + 12
        this.getProperties()
      }
      if (this.perPage >= this.totalItems) {
        this.toggleButtonMore = false
      }
    },

    getStatus (key) {
      var status = '-'
      switch (key) {
        case 'under_construction':
          status = 'Em construÃ§Ã£o'
          break
        case 'release':
          status = 'LanÃ§amento'
          break
        case 'ready_live':
          status = 'Pronto para morar'
          break
        case 'ready_build':
          status = 'Pronto para construir'
          break
        case 'pre_release':
          status = 'PrÃ© LanÃ§amento'
          break
        case 'done':
          status = 'Pronto'
          break
        case 'sold':
          status = '100% vendido'
          break
      }
      return status
    },

    retrieveValuesFromHome (state, city) {
      this.states = this.$route.params.states
      this.cities = this.$route.params.cities
      this.stateSelected = state
      this.citySelected = city
      this.benefitsSelected = this.firstValue
    },

    async getProperties () {
      this.loader = true
      try {
        const params = {
          search: this.searchText,
          state_id: this.stateSelected.id,
          region_id: this.citySelected.id,
          price: this.sortType,
          feature_id: this.benefitsSelected.id,
          currentPage: this.currentPage,
          perPage: this.perPage,
          is_visible: true
        }

        const pagination = {
          perPage: this.perPage,
          page: this.currentPage
        }

        const properties = await propertiesService.listProperties(params, pagination)
        this.totalItems = properties.data.meta.count
        this.totalPages = properties.data.meta.pages
        this.currentPage = properties.data.meta.page
        this.properties = properties.data.data
        this.loader = false

        if (properties.data.data.length < 1) {
          this.empty = true
        } else {
          this.empty = false
        }
      } catch (error) {
        console.log('Ocorreu um erro inesperado.')
      }
    },

    async getStates () {
      this.loader = true
      try {
        const response = await regionsService.retrieveStates()
        this.states = [].concat.apply(this.states, response.data.data)
        this.loader = false
        if (response.data.data.length < 1) {
          this.empty = true
        } else {
          this.empty = false
        }
      } catch {
        console.log('Ocorreu um erro inesperado.')
      }
    },

    async getCities (id) {
      this.loader = true
      try {
        const params = { state_id: id }
        const response = await regionsService.retrieveRegions(params)
        this.cities = [].concat.apply([this.firstValue], response.data.data)
        this.loader = false
        if (response.data.data.length < 1) {
          this.empty = true
        } else {
          this.empty = false
        }
      } catch {
        console.log('Ocorreu um erro inesperado.')
      }
    },

    async getBenefits () {
      this.loader = true
      try {
        const response = await propertiesService.getBenefits()
        this.benefits = [].concat.apply([this.firstValue], response.data.data)
        this.loader = false
      } catch {
        console.log('Ocorreu um erro inesperado.')
      }
    },

    searchChange (text) {
      this.getProperties()
    },

    stateChange: function () {
      const id = this.stateSelected.id
      this.citySelected = this.cities[0]
      this.getCities(id)
      this.getProperties()
    },

    cityChange: function () {
      this.getProperties()
    },

    orderChange: function () {
      this.getProperties()
    },

    benefitsChange: function () {
      this.getProperties()
    }
  }
}
</script>

